body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search-root {
    width: calc(70%);
    margin-left: 2rem !important;
    padding: 2px 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.input {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.icon-button {
    padding: 10px;
}
.divider {
    height: 20px;
    margin: 4px;
}

.drawer {
    width: 100px !important;
}

.exp-width {
    width: 19rem;
}

.heading {
    font-size: 2rem;
}
.App {
  text-align: center;
  overflow-y: hidden;
}

.left-align {
  padding-right: 2rem;

}

.icon-dissapear {
  display: none;
}

.header-space-right {
  margin-left: calc(19%);
  width: calc(100% - 19%) !important;
}

.btn-right-space {
  margin-right: 2rem;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 1.25s linear;
          animation: App-logo-spin infinite 1.25s linear;
  background-color: rgba(39,62,84,0.82);
  overflow: hidden;
  height: 10rem;
  width: 10rem;
  z-index: 20;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  -webkit-transition: transform 0.5s ease-in-out;
}

.App-link {
  color: darkturquoise;
  font-size: 1.25rem;

}

@-webkit-keyframes App-logo-spin {
  0%{
    -webkit-transform: scale(0.85);
    transform: scale(0.85);
    background: rgba(254,0,50,0.50);
    overflow: hidden;
    z-index: 2;
  }
  25% {
    -webkit-transform: scale(0.55);
    transform: scale(0.55);
    background: rgba(39,62,84,0.82);
    overflow: hidden;
    z-index: 2;
  }
  50% {
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
    background: rgba(254,0,50,0.50);
    overflow: hidden;
    z-index: 2;
  }
  100% {
    -webkit-transform: scale(0.85);
    transform: scale(0.85);
    background: rgba(39,62,84,0.82);
    overflow: hidden;
    z-index: 2;
  }
}

@keyframes App-logo-spin {
  0%{
    -webkit-transform: scale(0.85);
    transform: scale(0.85);
    background: rgba(254,0,50,0.50);
    overflow: hidden;
    z-index: 2;
  }
  25% {
    -webkit-transform: scale(0.55);
    transform: scale(0.55);
    background: rgba(39,62,84,0.82);
    overflow: hidden;
    z-index: 2;
  }
  50% {
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
    background: rgba(254,0,50,0.50);
    overflow: hidden;
    z-index: 2;
  }
  100% {
    -webkit-transform: scale(0.85);
    transform: scale(0.85);
    background: rgba(39,62,84,0.82);
    overflow: hidden;
    z-index: 2;
  }
}


.App-author {
  color: cyan;
  font-size: 1.25rem;
}

